.app-container {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  height: 100vh;
  background-size: cover;
  transition: background-image 0.5s ease-in-out;
}

.input-container {
  margin-bottom: 20px;
}

.input-container input {
  padding: 10px;
  font-size: 16px;
  border-radius: 5px;
  border: 1px solid #ccc;
}

.weather-container {
  background-color: rgba(255, 255, 255, 0.8);
  padding: 20px;
  border-radius: 10px;
}

.weather-info {
  text-align: left;
  color: #000;
}

.background-sunny {
  background-image: url('https://via.placeholder.com/1920x1080/FFD700/000000?text=Sunny');
}

.background-rainy {
  background-image: url('https://via.placeholder.com/1920x1080/808080/000000?text=Rainy');
}

.background-cloudy {
  background-image: url('https://via.placeholder.com/1920x1080/87CEEB/000000?text=Cloudy');
}

.background-snowy {
  background-image: url('https://via.placeholder.com/1920x1080/FFFFFF/000000?text=Snowy');
}
